.App {
  text-align: center;
}
.Navbar .Nav {
  color: #444851;
  font-weight: 600;
  font-size: 35px;
}
.Jumbotron {
  height: 500px;
  background-image: url("https://orcite.co.kr/web/upload/mundane/main_banner_1.jpg");
  background-size: 100%;
  background-repeat: no-repeat;
  opacity: .9;
  color: whitesmoke; 
  background-position: fixed;
}
.Jumbotron h {

}
.Jumbotron p {
  font-size: 20px;
  color: whitesmoke;
}
.Jumbotron Button {
  background-color: palevioletred;
  color: white;
  border: 3px solid palevioletred;
  box-sizing: border-box;
}
.Jumbotron Button:hover {
  background-color: transparent;
  border: 3px solid palevioletred;
  color: palevioletred;
  box-sizing: border-box;
}

.Detail__name{
  padding: 20px;
  margin: 10px;
}
.Detail__alert{
  padding: 10px 20px;
  margin: 20px;
  background-color: antiquewhite;
  display: block;
}
.wow-enter{
  opacity: 0;
}
.wow-enter-active{
  opacity: 1;
  transition: all 500ms;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
